import { screenDesign } from "../actionType"


export const updateScreenDetails = (data) => (dispatch, getState) => {

    dispatch({ type: screenDesign.updateScreenDetails, payload: data })
}


export const updateSelectComponent = (data) => (dispatch, getState) => {

    dispatch({ type: screenDesign.updateSelectComponent, payload: data })
}

export const updateRightScreenLayout = (data) => (dispatch, getState) => {

    dispatch({ type: screenDesign.updateRightScreenDetails, payload: data })

}