import React, { Component } from 'react'

export class Image extends Component {

    getImageUrl = () => {
        let { url } = this.props

        if (typeof url === 'string' || url instanceof String) {
            return url
        } else {
            return URL.createObjectURL(url)
        }

    }

    render() {

        let { className } = this.props

        return (
            <img
                src={this.getImageUrl()}
                className={className}
            />
        )
    }
}
