import React, { Component } from 'react'
import * as ChildrenComponent from "../childrenComponents"

export class Row extends Component {


    renderComponent = (data) => {

        const Component = ChildrenComponent[data.component]
        return <Component
            details={data}
        />

    }

    render() {

        let { items } = this.props.details
            console.log(items,'5d4s5ds')
        return (
            <div className="row border-top w-100 flex-grow-1 no-gutters">

                {items.map((data,index ) => {
                    return (
                        <div className={`col-6 ${index>0 ? 'border-left' :'' }`}>
                            {this.renderComponent(data)}
                        </div>
                    )
                })}

               
            </div>
        )
    }
}
