import { screenDesign } from "../actionType"


const initialState = {
    currentScreeDesignDetails: {},
    currentRightSectionLayout: 'LayoutViews',
    currentSelectComponent: {}
}

export default (state = Object.assign({}, initialState), { type, payload }) => {
    switch (type) {
        case screenDesign.updateScreenDetails:
            return {
                ...state,
                currentScreeDesignDetails: payload
            }
        case screenDesign.updateRightScreenDetails:
            return {
                ...state,
                currentRightSectionLayout: payload
            }
        case screenDesign.updateSelectComponent:
            return {
                ...state,
                currentSelectComponent: payload
            }
        default:
            return state
    }

}