import React, { Component } from 'react'
import { updateRightScreenLayout } from "../../../redux/action/screenDesign"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"

export class LayoutViewsClass extends Component {

    state = {
        layoutDetals: [
            {
                key: 'Fixed',
                name: 'Fixed'
            },
            {
                key: 'Scroll',
                name: 'Scroll'
            },
        ]
    }


    handleSelect = (key) => {

        this.props.updateSelections('type', key)

        // this.props.updateRightScreenLayout(key == 'Fixed' ? 'AllSection' : null)
        this.props.updateRightScreenLayout( null)

    }

    render() {

        let { layoutDetals } = this.state

        let { updateSelections } = this.props

        return (
            <div className="flex-with-center">
                {layoutDetals.map(({ key, name }, index) => (
                    <div
                        className="border-box-section flex-with-center cursor-pointer mr-2"
                        onClick={e => this.handleSelect(key)}
                        key={index}
                    >
                        <p>{name}</p>
                    </div>
                ))}
            </div>
        )
    }
}


const mapStateToProps = state => {
    return {

    };
};

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        updateRightScreenLayout
    }, dispatch)
}

export const LayoutViews = connect(mapStateToProps, mapDispatchToProps)(LayoutViewsClass)
