import React, { Component } from 'react'
import * as MainComponent from "../mainComponents"
import { AddNew } from "../mainComponents/addNew"
import { handleSelect } from "../../../services/helperFunctions"
import { updateRightScreenLayout, updateSelectComponent, updateScreenDetails } from "../../../redux/action/screenDesign"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'
import MobileDragWrapper from "./mobileDragWrapper"
import _ from "lodash"
export class MobileScreenClass extends Component {

    componentDidMount() {
        console.log(this.props)
    }

    componentWillReceiveProps(props) {
        console.log(props)
    }

    renderComponent = (data, index, provided, snapshot) => {

        const Component = MainComponent[data.component]
        return <Component
            details={data}
            handleOnClick={this.handleOnClick}
            index={index}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
            innerRef={provided.innerRef}
            isDragging={snapshot.isDragging}
        />


    }

    handleOnClick = (e, details) => {

        this.props.updateRightScreenLayout('AllSection')

        handleSelect(e)

        this.props.updateSelectComponent(details)

    }

    onDragEnd = (result) => {

        const { destination, source, draggableId } = result

            
        let currentScreeDesignDetails = _.cloneDeep(this.props.currentScreeDesignDetails)

        if (!destination) {
            return
        }

        if (destination.index === source.index) {
            return
        }

        let items = currentScreeDesignDetails.items


        let sourceElement = items[source.index]     

        items.splice(source.index, 1)

        items.splice(destination.index, 0, sourceElement)              
        
        currentScreeDesignDetails.items = items

        this.props.updateScreenDetails(currentScreeDesignDetails)

    }



    render() {

        let { items = [], type } = this.props.currentScreeDesignDetails

        return (
            <div className="h-100 d-flex align-items-center  flex-column">
                <DragDropContext onDragEnd={this.onDragEnd}>
                    <Droppable droppableId={'viswa'} type="TASK">
                        {(provided, snapshot) => {
                            return (
                                <div
                                    className="w-100 h-100"
                                    ref={provided.innerRef}
                                    {...provided.droppableProps}
                                    isDraggingOver={snapshot.isDraggingOver}
                                >
                                    <MobileDragWrapper
                                        items={items}
                                        handleOnClick={this.handleOnClick}
                                    />

                                    {provided.placeholder}

                                    <AddNew />
                                </div>
                            )
                        }

                        }

                    </Droppable>

                </DragDropContext>


            </div>
        )
    }

}


const mapStateToProps = state => {
    return {
        currentScreeDesignDetails: state.screenDesign.currentScreeDesignDetails
    };
};

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        updateRightScreenLayout,
        updateSelectComponent,
        updateScreenDetails
    }, dispatch)
}

export const MobileScreen = connect(mapStateToProps, mapDispatchToProps)(MobileScreenClass)
