import React, { Component } from 'react'
import { handleSelect } from "../../../services/helperFunctions"
import { updateRightScreenLayout, updateSelectComponent } from "../../../redux/action/screenDesign"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"
import { Draggable } from 'react-beautiful-dnd'

export class ServiceCardClass extends Component {

    componentDidMount() {
        console.log(this.props, '5s4ds5d')
    }

    render() {

        let { component, properties: { data } } = this.props.details

        let { index, innerRef, provided } = this.props


        return (
            <div
                className=" service-card service-card-mobile cursor-pointer row  align-items-center p-2  m-0"
                onClick={e => this.props.handleOnClick(e, this.props.details)}
                {...provided.draggableProps}
                {...provided.dragHandleProps}
                ref={innerRef}
            >
                {data.map(({ title }) => {
                    return (<div className="col-4">
                        <div className="service-icons-wrapper">
                            <img src={require('../../../assets/images/component/icon.png')} />

                        </div>
                        <p className="font-sm mb-0 mt-auto">{title}</p>
                    </div>)
                })}
            </div>
        )

    }
}

const mapStateToProps = state => {
    return {
    };
};

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        updateRightScreenLayout,
        updateSelectComponent
    }, dispatch)
}

export const ServiceCard = connect(mapStateToProps, mapDispatchToProps)(ServiceCardClass)

