import React, { Component } from 'react'
import { updateRightScreenLayout,updateSelectComponent } from "../../../redux/action/screenDesign"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"
import { removeAllSelect } from "../../../services/helperFunctions"
export default class AddNewClass extends Component {


    handleSelect = () => {

        removeAllSelect()

        this.props.updateSelectComponent({})

        this.props.updateRightScreenLayout('AllSection')

    }

    render() {
        return (
            <div
                className="add-new-card cursor-pointer text-center"
                onClick={this.handleSelect}
            >
                <p>Add New</p>
            </div>
        )
    }
}


const mapStateToProps = state => {
    return {

    };
};

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        updateRightScreenLayout,
        updateSelectComponent
    }, dispatch)
}

export const AddNew = connect(mapStateToProps, mapDispatchToProps)(AddNewClass)