
const uuid = require('uuid/v1');

export const spreedStyle = (data) => {

    let temp = {}

    data.map((style) => {
        temp = {
            ...temp,
            ...style
        }
    })

    return temp

}

export const handleSelect = (e)=>{
    let elements = document.getElementsByClassName('red-border')

    let len = elements.length

    for(let i=0; i<len;i++ ){
        elements[i].classList.remove('red-border')
    }
    
    e.target.classList.add('red-border')

}

export const removeAllSelect = (e)=>{
    let elements = document.getElementsByClassName('red-border')

    let len = elements.length

    for(let i=0; i<len;i++ ){
        elements[i].classList.remove('red-border')
    }


}
export const uniqueId = ()=>{
    return uuid()
}