import React, { Component } from 'react'
import { Draggable } from 'react-beautiful-dnd'
import * as MainComponent from "../mainComponents"

export default class mobileDragWrapper extends Component {

    renderComponent = (data, index,provided, snapshot) => {

        const Component = MainComponent[data.component]
        return <Component
            details={data}
            handleOnClick={this.props.handleOnClick}
            index={index}
            provided={provided}
            {...provided.dragHandleProps}
            innerRef={provided.innerRef}
            isDragging={snapshot.isDragging}
        />


    }
  render() {

    let { items} = this.props

    return (
        items.map((data,index) => (                                

            <Draggable
            draggableId={data.uuid}  index={index}                                   
            >
             {(provided, snapshot) => (
                this.renderComponent(data,index,provided, snapshot)
             )}

            </Draggable>
             

        ))
    )
  }
}
