import React, { Component } from 'react'
import { Image } from "../../helperComponent"
import { Draggable } from 'react-beautiful-dnd'

export class Banner extends Component {

    render() {

        let { details: { component, properties: { data } }, index, innerRef, provided } = this.props

        return (
            <div
                className="banner-img-mobile cursor-pointer"
                onClick={e => this.props.handleOnClick(e, this.props.details)}
                {...provided.draggableProps}
                {...provided.dragHandleProps}
                ref={innerRef}
            >
                <Image
                    url={data.img}
                    className="img-fluid no-pointer-event"
                />
            </div>
        )
    }
}
