import React, { Component } from 'react'
import { updateRightScreenLayout, updateScreenDetails, updateSelectComponent } from "../../../redux/action/screenDesign"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"
import _ from "lodash"
import { uniqueId } from "../../../services/helperFunctions"


export class ServiceCardClass extends Component {

    state = {
        details: {
            "key": 1,
            "component": "ServiceCard",
            "type": "component",
            "properties": {
                "columns": 3,
                "title": "Features",
                "data": [
                    { "title": "Credit TopUp", "icon": "alarm", "show": false, "navigateTo": "" },
                    { "title": "Digital Goods and Services", "icon": "fingerprint", "show": true, "navigateTo": "" },
                    { "title": "TopUp for Friends", "icon": "face", "show": true, "navigateTo": "" },
                    { "title": "Pay Bill For Friends", "icon": "payment", "show": true, "navigateTo": "" },
                    { "title": "GO-Life Insurance", "icon": "accessible", "show": true, "navigateTo": "" },
                    { "title": "Addons", "icon": "android", "show": true, "navigateTo": "" },
                    { "title": "Roaming and IDD", "icon": "assignment", "show": false, "navigateTo": "" },
                ],
            }
        },
        properties: {
            size: ['x1', 'x2', 'x3']
        },
        active: false

    }

    componentDidMount() {

        this.getSelectedComponent(this.props)

    }

    componentWillReceiveProps(props) {

        this.getSelectedComponent(props)

    }

    getSelectedComponent = ({ currentSelectComponent }) => {

        let { details: { component } } = this.state

        this.setState({
            active: currentSelectComponent.component == component
        })

    }

 
    render() {

        let { active, details: { properties: { data } } } = this.state

        return (
            <>
                {active && <p>Active</p>}

                <div 
                  className="border service-card cursor-pointer row no-gutters align-items-center p-2" 
                  onClick={e => this.props.handleSelect(this.state.details)}
                  >
                    {data.map(({ title }) => {
                        return (<div className="col-4">
                            <div className="service-icons-wrapper">
                                <img src={require('../../../assets/images/component/icon.png')} />

                            </div>
                            <p className="font-sm m-0">{title}</p>
                        </div>)
                    })}
                </div>

            </>
        )
    }
}

const mapStateToProps = state => {
    return {
        currentScreeDesignDetails: state.screenDesign.currentScreeDesignDetails,
        currentSelectComponent: state.screenDesign.currentSelectComponent
    };
};

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        updateRightScreenLayout,
        updateScreenDetails,
        updateSelectComponent
    }, dispatch)
}

export const ServiceCard = connect(mapStateToProps, mapDispatchToProps)(ServiceCardClass)

