import React, { Component } from 'react'
import * as ChildrenComponent from "../childrenComponents"
export  class Col extends Component {


    renderComponent = (data) => {

        const Component = ChildrenComponent[data.component]
        return <Component
            details={data}
        />

    }

  render() {

    let { items } = this.props.details

    return (
      <div className="flex-with-center w-100 flex-grow-1">
        {items.map((data)=>{
                return  this.renderComponent(data)
        })}
      </div>
    )
  }
}
