import React, { Component } from 'react'
import { handleSelect } from "../../../services/helperFunctions"
import { updateRightScreenLayout, updateSelectComponent } from "../../../redux/action/screenDesign"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"

export class CardClass extends Component {

    componentDidMount() {
        console.log(this.props, '5s4ds5d')
    }


    render() {

        let { innerRef, provided } = this.props


        return (
            <div
                className="mobile-card cursor-pointer"
                onClick={(e) => {
                    this.props.handleOnClick(e, this.props.details)
                }}

                {...provided.draggableProps}
                {...provided.dragHandleProps}
                ref={innerRef}
            >

            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
    };
};

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        updateRightScreenLayout,
        updateSelectComponent
    }, dispatch)
}

export const Card = connect(mapStateToProps, mapDispatchToProps)(CardClass)

