import React, { Component } from 'react'
import * as Section from "../section/index"
import * as ScrollSelection from "../scrollSelection"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"
import _ from "lodash"
import { updateRightScreenLayout, updateScreenDetails, updateSelectComponent } from "../../../redux/action/screenDesign"
import { uniqueId } from "../../../services/helperFunctions"


export class AllSectionClass extends Component {


  handleSelect = (details) => {

     details = _.cloneDeep(details)

    let { currentScreeDesignDetails,
      updateScreenDetails,
      updateRightScreenLayout,
      currentSelectComponent
    } = this.props

    console.log(currentSelectComponent,details,'45ds4d5s4d')

    if (currentSelectComponent.component != details.component) {

      if (Object.keys(currentSelectComponent).length) {

        this.replaceComponent(details)

        updateRightScreenLayout(null)

      } else {

        let temp = _.cloneDeep(currentScreeDesignDetails)

        details.uuid = uniqueId()

        if (temp.items) {
          temp.items.push(details)
        } else {
          temp.items = []
          temp.items.push(details)
        }

        updateScreenDetails(temp)

        updateRightScreenLayout(null)

      }

    }

  }

  replaceComponent(details) {

    let { currentSelectComponent: { uuid } } = this.props

     details = Object.assign({}, details)


    details.uuid = uniqueId()

    let currentScreeDesignDetails = _.cloneDeep(this.props.currentScreeDesignDetails)

    let index = currentScreeDesignDetails.items.findIndex((data) => data.uuid == uuid)

    if (index !== -1) {

        currentScreeDesignDetails.items[index] = details

        this.props.updateScreenDetails(currentScreeDesignDetails)

        this.props.updateSelectComponent(details)

    }
}


  render() {

    let { currentScreeDesignDetails: { type = 'Fixed' } } = this.props

    return (
      <div className="d-flex align-items-center flex-wrap">

        {type == 'Fixed' ? Object.keys(Section).map((name, index) => {

          let Component = Section[name]

          return (
            <div className="m-2">
              <Component 
               key={index} 
               {...this.props} 
               replaceComponent={this.replaceComponent}
               handleSelect={this.handleSelect}
               />
            </div>
          )
        }) : Object.keys(ScrollSelection).map((name, index) => {

          let Component = ScrollSelection[name]

          return (
            <div className="m-2">
              <Component 
               key={index} 
               {...this.props}
               replaceComponent={this.replaceComponent}
               handleSelect={this.handleSelect}
                />
            </div>
          )
        })}


      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
    currentScreeDesignDetails: state.screenDesign.currentScreeDesignDetails,
    currentSelectComponent: state.screenDesign.currentSelectComponent
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    updateRightScreenLayout,
     updateScreenDetails,
      updateSelectComponent
  }, dispatch)
}

export const AllSection = connect(mapStateToProps, mapDispatchToProps)(AllSectionClass)


