import React, { Component } from 'react'
import { Image } from "../../helperComponent"
export class Banner extends Component {

    state = {
        details: {
            "key": 1,
            "component": "Banner",
            "type": "component",
            "properties": {
                "columns": 3,
                "title": "Features",
                "data": {
                    img: require('../../../assets/images/placeholder/bannerPlaceholder.jpg')
                },
            },
            "styles": [],
            active: false
        }
    }

    componentDidMount() {

        this.getSelectedComponent(this.props)

    }

    componentWillReceiveProps(props) {

        this.getSelectedComponent(props)

    }

    getSelectedComponent = ({ currentSelectComponent }) => {

        let { details: { component } } = this.state

        this.setState({
            active: currentSelectComponent.component == component
        })

    }

    render() {

        let { details: { properties: { data } }, active } = this.state

        return (
            <>
                {active && <p>Active</p>}
                <div
                    className="banner-img-pc cursor-pointer"
                    onClick={e => this.props.handleSelect(this.state.details)}
                >
                    <Image
                        url={data.img}
                    />
                </div>
            </>
        )
    }
}
