import React, { Component } from 'react'
import { Row, Col } from 'reactstrap';
import * as LayoutComponents from "./components/pc/selection"
import * as MobileComponents from "./components/mobile/layout"
import AllSection from "./components/pc/selection/allSelections"
import { connect } from 'react-redux'
import { bindActionCreators } from "redux"
import { updateScreenDetails } from "./redux/action/screenDesign"
import _ from "lodash"
import PerfectScrollbar from 'react-perfect-scrollbar'
class AppClass extends Component {

  constructor(props) {
    super(props)
    this.state = {
      currentComponent: ''
    }
  }

  componentWillMount() {

    this.getRightSectionComponent()

    this.updateCurrentRightSection(this.props)
    
  }

  componentWillReceiveProps(props) {

    this.updateCurrentRightSection(props)
 
  }

  updateCurrentRightSection = (props) =>{

    let { currentRightSectionLayout } = props

    this.setState({
      currentComponent:currentRightSectionLayout
    })

  }


  getRightSectionComponent = () => {

    let { currentScreeDesignDetails } = this.props

    let currentComponent;

    this.setState({
      currentComponent
    })

  }
 

  updateSelections = (key, payload, array) => {

    let { updateScreenDetails } = this.props

    let { currentScreeDesignDetails } = _.cloneDeep(this.props)

    currentScreeDesignDetails[key] = payload

    console.log(payload)

    updateScreenDetails(currentScreeDesignDetails)

  }

  renderLeftSection = () => {

    let { currentScreeDesignDetails } = this.props

    let { items = [] } = currentScreeDesignDetails

    if (Object.keys(currentScreeDesignDetails).length) {

      const Component = MobileComponents.MobileScreen
      return <Component
        updateSelections={this.updateSelections}
        currentScreeDesignDetails={currentScreeDesignDetails}
      />


    }

    return ''

  }

  renderRightSection = () => {

    let { currentComponent } = this.state

    if (currentComponent) {

      const Component = LayoutComponents[currentComponent];

      return <Component
        updateSelections={this.updateSelections}
      />

    }

    return ''

  }

  render() {

    let { currentScreeDesignDetails:{ type='' } } = this.props

    return (
      <div className="main-wrapper-section">

        <Row noGutters className="h-100">


          <Col md="3" className="p-3 flex-with-center">

            <div className={`mobile-wrapper-section ${type=='Scroll' ? 'scroll' : ''}`}>
            <PerfectScrollbar>
              {this.renderLeftSection()}
              </PerfectScrollbar>
            </div>

          </Col>
          <Col md="9" className="p-3 flex-with-center">
            <div className="right-section-wrapper">
              {this.renderRightSection()}
            </div>
          </Col>

        </Row>

      </div>
    )
  }
}




const mapStateToProps = state => {
  return {
    currentScreeDesignDetails: state.screenDesign.currentScreeDesignDetails,
    currentRightSectionLayout: state.screenDesign.currentRightSectionLayout,
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    updateScreenDetails
  }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(AppClass)

