import React, { Component } from 'react'
import { spreedStyle } from "../../../services/helperFunctions"
import { handleSelect } from "../../../services/helperFunctions"
export class View extends Component {

  handleOnClick = (e) => {
    handleSelect(e)
  }
  
  render() {

    let { styles } = this.props.details


    return (
      <div
        className="h-100 w-100 cursor-pointer"
        style={spreedStyle(styles)}
        onClick={(e) => {
          this.handleOnClick(e)
        }}
      >

      </div>
    )
  }
}
